@import 'vars';
@import '_typography';
@import 'basic';
@import 'tables';

body {
  background: #16181C;
}


* {
  box-sizing:border-box;
  user-select:none;
}

.App {
  display: flex;
  flex-direction: row;
  &-logo {
    height: 400px;
    pointer-events: none;
  }
  &-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    h2 {
      color:#61dafb7d;
    }
  }
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}




@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}






.logo {
  height: 65px;
  display:flex;
  padding-left: 20px;
}


